/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
  Button,
  Form,
  Input,
  Row,
  Col,
  Popconfirm,
  Modal,
  Spin,
  Typography,
  Select,
  Tag,
  Descriptions,
  message,
} from "antd";
import {
  DeleteOutlined,
  EditOutlined,
  LoadingOutlined,
  EyeOutlined,
  UserOutlined,
} from "@ant-design/icons";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
  useRef,
} from "react";
import service from "../../../services/orders";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import OrderDetail from "./OrderDetail";

export default function Order({ type, categoryId = null }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState([]);
  const [qData, setQData] = useState({ page: 1, limit: 20, type, categoryId });
  const attributeModalRef = useRef();
  const statusModalRef = useRef();

  const columns = [
    {
      title: "Order id",
      dataIndex: "orderId",
      // width: 200,
    },
    {
      title: "Shipping Address",
      dataIndex: "addressDetails",
      width: 250,
      render: (_) => {
        const data = JSON.parse(_);
        return <span>{data.address + " " + data.state + " " + data.pin}</span>;
      },
    },
    {
      title: "Total Amount",
      dataIndex: "priceDetails",
      width: 140,
      render: (_) => {
        return <span>₹{_.finalPrice}</span>;
      },
    },
    {
      title: "Credit",
      dataIndex: "priceDetails",
      width: 140,
      render: (_) => {
        return _?.isCredit ? (
          <Tag color="red">True</Tag>
        ) : (
          <Tag color="blue">False</Tag>
        );
      },
    },
    {
      title: "Status",
      dataIndex: "status",
      width: 200,
      render: (_, { status }) => {
        if(status === "canceled" || status === "failed"){
          return <Tag color="red">{status}</Tag>
        }else{
          return <Tag color="green">{status}</Tag>
        }
      },
    },
    {
      title: "Action",
      dataIndex: "_id",
      width: 70,
      render: (v, row) => {
        return (
          <>
            <Button
              type="primary"
              size="small"
              icon={<UserOutlined />}
              onClick={() => {
                statusModalRef.current.openForm(row);
              }}
            />
          </>
        );
      },
    },
  ].filter((item) => !item.hidden);

  function list() {
    setLoading(true);
    service
      .orderList(qData)
      .then((res) => {
        setData(res.data?.map((v) => ({ ...v, key: v._id })));
        setQData({
          ...qData,
          limit: res.extra.limit,
          page: res.extra.page,
          total: res.extra.total,
        });
      })
      .catch((err) => {})
      .finally(() => {
        setLoading(false);
      });
  }

  const deleteData = (id) => {
    service
      .orderDelete(id)
      .then((res) => {
        AntdMsg(res.message);
        list();
        setSelected([]);
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      });
  };

  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";

  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    title: () => (
      <Search
        {...{
          selected,
          deleteData,
          qData,
          setQData,
          list,
          categoryId,
        }}
      />
    ),
    showHeader: true,
    footer: () => <Pagination {...{ qData, setQData }} />,
    rowSelection: {
      onChange: (selectedRowKeys) => {
        setSelected(selectedRowKeys);
      },
    },
    tableLayout: undefined,
  };

  useEffect(() => {
    list();
  }, [qData.page, qData.limit]);

  return (
    <>
      {!categoryId ? (
        <Typography.Title level={5}>List of Orders</Typography.Title>
      ) : null}

      <Table
        {...tableProps}
        pagination={{ position: ["none"], pageSize: qData.limit }}
        columns={tableColumns}
        dataSource={data.length ? data : []}
        scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
        expandable={{
          expandedRowRender: (record) => (
            <>
              <Descriptions column={4} title="User Info">
                <Descriptions.Item label="UserName">
                  {record?.userDetails
                    ? record?.userDetails.name
                    : "Not Accepted"}
                </Descriptions.Item>
                <Descriptions.Item label="Email">
                  {record?.userDetails ? record?.userDetails.email : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item label="Phone">
                  {record?.userDetails ? record?.userDetails.phone : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item label="Price">
                  {record?.priceDetails ? record?.priceDetails.price : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item label="Company">
                  {record?.userDetails ? record?.userDetails.company : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item label="GST">
                  {record?.userDetails ? record?.userDetails.gst : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item label="Address">
                  {record?.addressDetails !== null ? (
                    <span>
                      {JSON.parse(record?.addressDetails)?.address +
                        " , " +
                        JSON.parse(record?.addressDetails)?.state +
                        " , " +
                        JSON.parse(record?.addressDetails)?.pin}
                    </span>
                  ) : (
                    <span>Not Mentioned</span>
                  )}
                </Descriptions.Item>
                <Descriptions.Item label="Final Price">
                  {record?.priceDetails
                    ? record?.priceDetails.finalPrice
                    : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item span={3}></Descriptions.Item>
                <Descriptions.Item label="Discount Price">
                  {record?.priceDetails
                    ? record?.priceDetails.discountPrice
                    : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item span={3}></Descriptions.Item>
                <Descriptions.Item label="Coupon Discount Price">
                  {record?.priceDetails
                    ? record?.priceDetails.couponDiscountPrice
                    : "Empty"}
                </Descriptions.Item>
                <Descriptions.Item span={3}></Descriptions.Item>
                <Descriptions.Item label="IsCredit">
                  {record?.priceDetails?.isCredit ? "True" : "False"}
                </Descriptions.Item>
              </Descriptions>
              <Descriptions title="Order Details">
                <Descriptions.Item span={3}>
                  {" "}
                  <OrderDetail {...{ orderId: record._id }} />{" "}
                </Descriptions.Item>
              </Descriptions>
            </>
          ),
          rowExpandable: (record) => record.userDetails !== "Not Expandable",
        }}
      />
      <StatusForm ref={statusModalRef} {...{ list}}/> 
    </>
  );
}

function Search({ selected, deleteData, qData, setQData, list }) {
  return (
    <Form onSubmitCapture={list} className="search-form">
      <Row gutter={[12, 2]}>
        <Col span={4}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Input
              placeholder="Search by order id"
              value={qData.key}
              onChange={(e) => setQData({ ...qData, key: e.target.value })}
              allowClear
            />
          </Form.Item>
        </Col>
        <Col span={3}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Button type="primary" htmlType="submit">
              Search
            </Button>
          </Form.Item>
        </Col>
        <Col span={5}>
          {selected.length ? (
            <Popconfirm
              title="Are you sure to delete these selected Order?"
              onConfirm={() => {
                deleteData(selected);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                danger
                style={{ float: "right" }}
                icon={<DeleteOutlined />}
              >
                Delete Selected
              </Button>
            </Popconfirm>
          ) : null}
        </Col>
      </Row>
    </Form>
  );
}

const StatusForm = forwardRef((props, ref) => {
  const { list, categoryId } = props;

  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });
  const [ajxRequesting, setAjxRequesting] = useState(false);

  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(
        dt
          ? {
              ...dt,
              ocassion: [],
              flavour: [],
              recipient: [],
              relation: [],
              specializationIds: [],
            }
          : {
              isPremium: false,
              isBestSelling: false,
              forBirthday: false,
              forAnniversary: false,
              isBestSellingCombos: false,
              variants: [{}],
              ocassion: [],
              flavour: [],
              recipient: [],
              relation: [],
              specializationIds: [],
              categoryIds: [categoryId],
            }
      );
    },
  }));

  const save = () => {
    setAjxRequesting(true);
    service
      .orderSave(data)
      .then((res) => {
        message.success(res.message);
        list();
        setOpen(false);
      })
      .catch((err) => {
        if (typeof err.message === "object") {
          let dt = err.message[Object.keys(err.message)[0]];
          message.error(dt);
        } else {
          AntdMsg(err.message, "error");
        }
      })
      .finally(() => {
        setAjxRequesting(false);
      });
  };
  return (
    <>
      <Modal
        title={(!data._id ? "Add New" : "Edit") + " Order Status"}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onOk={save}
        okButtonProps={{ disabled: ajxRequesting }}
        onCancel={() => {
          setOpen(false);
        }}
        destroyOnClose
        maskClosable={false}
        width={700}
        height={500}
        className="app-modal-body"
        footer={[
          <Button
            key="cancel"
            onClick={() => {
              setOpen(false);
            }}
          >
            Cancel
          </Button>,
          <Button key="save" type="primary" onClick={save}>
            Save
          </Button>,
        ]}
      >
        <Spin
          spinning={ajxRequesting}
          indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
        >
          <Form layout="vertical">
            <Col span={12}>
              <Form.Item label="Status" required>
                <Select
                  mode="single"
                  value={data.status}
                  onChange={(e) => {
                    setData({ ...data, status: e });
                  }}
                >
                  <Select.Option value={"initiated"}>Initiated</Select.Option>
                  <Select.Option value={"payment-success"}>Payment Success</Select.Option>
                  <Select.Option value={"delivered"}>Delivered</Select.Option>
                  <Select.Option value={"failed"}>Failed</Select.Option>
                  <Select.Option value={"shipped"}>Shipped</Select.Option>
                  <Select.Option value={"canceled"}>Canceled</Select.Option>
                </Select>
              </Form.Item>
            </Col>
          </Form>
        </Spin>
      </Modal>
    </>
  );
});

