/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, Divider, Card, Radio,Tabs } from "antd";
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from "react";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import service from "../../../services/product";
import serviceBrand from "../../../services/brand";
import serviceChemical from "../../../services/chemicals";
import serviceGases from "../../../services/gas";
import serviceLabware from "../../../services/labware";
import serviceLifescience from "../../../services/lifescience";
import serviceLabinstrument from "../../../services/master";
import util from "../../../utils/util";
import Varient from "./Varient";
import UploadImage from "../../../utils/UploadImage";
import serviceChemicalAttribute from "../../../services/chemicalAttribute";


export default function Product({ parentId, categoryType }) {
	const [data, setData] = useState([]);
	const [loading, setLoading] = useState(false);
	const [selected, setSelected] = useState([]);
	const [brands, setBrands] = useState([]);
	const [chemicals, setChemicals] = useState([]);
	const [gases, setGases] = useState([]);
	const [labware, setLabware] = useState([]);
	const [lifescience, setLifescience] = useState([]);
	const [labinstrument, setLabinstrument] = useState([]);
	const [qData, setQData] = useState({ page: 1, limit: 20, parentId, catType: categoryType });
	const addNewModalRef = useRef();
	const addNewLabelRef = useRef();
	const columns = [
		{
			title: "#",
			width: 40,
			render: (v, row, i) => <b>{i + 1}</b>,
		},
		{
			title: "Name",
			dataIndex: "name",
		},
		{
			title: "Variant",
			dataIndex: "varient",
			width: 100,
			render: (value, row) => {
				return (
					<Button
						onClick={() => {
							addNewLabelRef.current.openForm(row);
						}}
					>
						Variant
					</Button>
				);
			},
		},
		{
			title: "Slug",
			dataIndex: "slug",
			width: 150,
			render: (v) => (
				<Typography.Text ellipsis={{ expandable: true }}>{v}</Typography.Text>
			),
		},
		{
			title: "Starting Price",
			dataIndex: "startingPrice",
			width: 120,
			render: (v) => <b>₹ {v}</b>,
		},
		{
			title: "Description",
			dataIndex: "description",
			width: 250,
			render: (v) => (
				<Typography.Text ellipsis={{ expandable: true }}>{v}</Typography.Text>
			),
		},
		{
			title: "Status",
			dataIndex: "status",
			width: 80,
			render: (isActive) => {
				if (isActive) {
					return <Tag color="green">Active</Tag>;
				} else {
					return <Tag color="red">Inactive</Tag>;
				}
			},
		},
		{
			title: "Action",
			dataIndex: "_id",
			width: 70,
			render: (v, row) => {
				return (
					<>
						<Popconfirm
							title="Are you sure to delete this product?"
							onConfirm={() => {
								deleteData(row._id);
							}}
							okText="Yes"
							cancelText="No"
						>
							<Button
								type="primary"
								size="small"
								danger
								icon={<DeleteOutlined />}
							/>
							&nbsp;
						</Popconfirm>
						<Button
							type="primary"
							size="small"
							icon={<EditOutlined />}
							onClick={() => {
								addNewModalRef.current.openForm(row._id);
							}}
						/>
					</>
				);
			},
		},
	].filter((item) => !item.hidden);
	function list() {
		setLoading(true);
		service
			.listUser(qData)
			.then((res) => {
				setData(res.data?.map((v) => ({ ...v, key: v._id })));
				setQData({
					...qData,
					limit: res.extra.limit,
					page: res.extra.page,
					total: res.extra.total,
				});
			})
			.catch((err) => { })
			.finally(() => {
				setLoading(false);
			});
	}
	const deleteData = (id) => {
		service
			.deleteUser(id)
			.then((res) => {
				AntdMsg(res.message);
				list();
				setSelected([]);
			})
			.catch((err) => {
				AntdMsg(err.message, "error");
			});
	};
	const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
	tableColumns[0].fixed = true;
	tableColumns[tableColumns.length - 1].fixed = "right";
	const tableProps = {
		bordered: true,
		loading,
		size: "small",
		title: () => (
			<Search
				{...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
			/>
		),
		showHeader: true,
		footer: () => <Pagination {...{ qData, setQData }} />,
		rowSelection: {
			onChange: (selectedRowKeys) => {
				setSelected(selectedRowKeys);
			},
		},
		tableLayout: undefined,
	};
	useEffect(() => {
		list();
		serviceBrand.list({ isAll: 1 }).then((res) => {
			setBrands(res.data.map((v) => ({ label: v.name, value: v._id })));
		});
		serviceChemical.list({ isAll: 1 }).then((res) => {
			setChemicals(
				res.data.map((v) => ({
					label: v.name,
					value: v._id,
					list,
					parentId: v.parentId,
				}))
			);
		});
		serviceGases.listUser({ isAll: 1 }).then((res) => {
			setGases(
				res.data.map((v) => ({
					label: v.name,
					value: v._id,
					parentId: v.parentId,
				}))
			);
		});
		serviceLabware.listUser({ isAll: 1 }).then((res) => {
			setLabware(
				res.data.map((v) => ({
					label: v.name,
					value: v._id,
					parentId: v.parentId,
				}))
			);
		});
		serviceLabinstrument.listUser({ isAll: 1 }).then((res) => {
			setLabinstrument(
				res.data.map((v) => ({
					label: v.name,
					value: v._id,
					parentId: v.parentId,
				}))
			);
		});
		serviceLifescience.list({ isAll: 1 }).then((res) => {
			setLifescience(
				res.data.map((v) => ({
					label: v.name,
					value: v._id,
					parentId: v.parentId,
				}))
			);
		});
	}, [qData.page, qData.limit]);
	return (
		<>
			{!parentId ? (
				<Typography.Title level={5} style={{ marginTop: 0 }}>
					Product List
				</Typography.Title>
			) : null}
			<Table
				{...tableProps}
				pagination={{ position: ["none"], pageSize: qData.limit }}
				columns={tableColumns}
				dataSource={data.length ? data : []}
				scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
			/>
			<AddForm
				ref={addNewModalRef}
				{...{
					list,
					parentId,
					brands,
					chemicals,
					gases,
					labware,
					labinstrument,
					lifescience,
					categoryType
				}}
			/>
			<AddLabel ref={addNewLabelRef} {...{ list }} />
		</>
	);
}
function Search({
	addNewModalRef,
	selected,
	deleteData,
	qData,
	setQData,
	list,
}) {
	const onChange = (v, key) => {
		qData[key] = v;
		if (v === undefined || v === "") {
			delete qData[key];
			list();
		}
	};

	return (
		<Form onSubmitCapture={list} className="search-form">
			<Row gutter={[12, 2]}>
				<Col span={4}>
					<Form.Item style={{ marginBottom: 0 }}>
						<Input
							placeholder="Search by product name"
							value={qData.key}
							onChange={(e) => onChange(e.target.value, "key")}
							allowClear
						/>
					</Form.Item>
				</Col>
				<Col span={3}>
					<Form.Item style={{ marginBottom: 0 }}>
						<Button type="primary" htmlType="submit">
							Search
						</Button>
					</Form.Item>
				</Col>
				<Col span={5}>
					<Form.Item style={{ marginBottom: 0, float: "right" }}>
						<Button
							type="primary"
							icon={<PlusOutlined />}
							onClick={() => {
								addNewModalRef.current.openForm();
							}}
						>
							Add New
						</Button>
					</Form.Item>
					{selected.length ? (
						<Popconfirm
							title="Are you sure to delete these selected data ?"
							onConfirm={() => {
								deleteData(selected);
							}}
							okText="Yes"
							cancelText="No"
						>
							<Button
								type="primary"
								danger
								style={{ float: "right" }}
								icon={<DeleteOutlined />}
							>
								Delete Selected
							</Button>
						</Popconfirm>
					) : null}
				</Col>
			</Row>
		</Form>
	);
}
const AddForm = forwardRef((props, ref) => {
	const {
		list,
		parentId,
		brands,
		chemicals,
		gases,
		labware,
		labinstrument,
		lifescience,
		categoryType
	} = props;
	const [open, setOpen] = useState(false);
	const [data, setData] = useState({ aspect: 1 });
	const [chemicalAttr, setChemicalAttr] = useState({});
	const [ajxRequesting, setAjxRequesting] = useState(false);
	const [thumbFile, setThumbFile] = useState([]);
	const [descriptionFile, setDescriptionFile] = useState([]);
	const [file, setFile] = useState([]);
	const [currentTab, setCurrentTab] = useState(0);

	const handleChange = (value) => {
		currentTab === 0 ? setData({ ...data, ...value }) : setChemicalAttr({...chemicalAttr, ...value});
	};
	useImperativeHandle(ref, () => ({
		openForm(dt) {
			setOpen(true);
			setData(dt ? { _id: dt } : { catType: categoryType});
		},
	}));
	const getDetails = () => {
		setAjxRequesting(true);
		service
			.detailUser(data._id)
			.then((res) => {
				setData(res.data || {});
				setFile(res.data.imgs.length ? [...res.data.imgs] : []);
				setThumbFile(res.data.thumbImg ? [res.data.thumbImg] : []);
				setDescriptionFile(res.data.descriptionImg ? [res.data.descriptionImg] : []);
			})
			.catch((err) => {
				AntdMsg(err.message, "error");
			})
			.finally(() => {
				setAjxRequesting(false);
			});
	};

	const getChemicalDetails = () => {
        setAjxRequesting(true);
        serviceChemicalAttribute
            .details(data?._id)
            .then((res) => {
                setChemicalAttr(res?.data || {});
            })
            .catch((err) => {
                AntdMsg(err.message, "error");
            })
            .finally(() => {
                setAjxRequesting(false);
            });
    };

	const save = () => {
		setAjxRequesting(true);
		(() => {
            return currentTab === 0
                ? service.saveUser(data) : serviceChemicalAttribute.save({...chemicalAttr,productId:data?._id});

        })().then(res => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
			
            setCurrentTab((currentTab + 1) % 2);
			data.isAttribute === true  ?  currentTab === 1 &&  handleClose() :  handleClose()
            list();
			})
			.catch((err) => {
				if (typeof err.message === "object") {
					let dt = err.message[Object.keys(err.message)[0]];
					AntdMsg(dt, "error");
				} else {
					AntdMsg(err.message, "error");
				}
			})
			.finally(() => {
				setAjxRequesting(false);
			});
	};
	useEffect(() => {
		if (data._id) {
			getDetails();
			getChemicalDetails({})
		}
	}, [data._id]);

	useEffect(() => {
		if (!data._id) {
			handleChange({ slug: util.removeSpecialChars(data.name || "") });
		}
	}, [data.name]);

	useEffect(() => {
		if (!data._id) {
			handleChange({ slug: util.removeSpecialChars(data?.name || "") });
		}
	}, [data.name]);

	useEffect(() => {
		setData({ ...data, thumbImg: thumbFile[0] });
	}, [thumbFile]);

	useEffect(() => {
		// if(categoryType ==='labware'){
		// }
		setData({ ...data, descriptionImg: descriptionFile[0] });
	}, [descriptionFile]);

	useEffect(() => {
		setData({ ...data, imgs: file });
	}, [file]);

	useEffect(() => {
		if (!open) {
			setData({});
		}
	}, [open]);

	const handleClose =()=>{
		setOpen(false);
		setCurrentTab(0)
	}

	return (
        <>
            <Modal
                title={(!data._id ? "Add New" : "Edit") + " Chemical Product"}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={handleClose}
                destroyOnClose
                maskClosable={false}
                width={1400}
                className="app-modal-without-footer-body-overflow1"
                footer={[
                    <Button key="cancel" onClick={handleClose}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>{data?.isAttribute ? currentTab === 1 ?  "Save" : 'Next' :  "Save"}</Button>
                ]}
            >
                <Spin
                    spinning={ajxRequesting}
                    indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
                >
                    <Tabs activeKey={currentTab} type="card" size="small" style={{ marginBottom: 0 }} onChange={(v) => { setCurrentTab(v); }}
                        items={[
                            {
                                label: "Basic Details",
                                key: 0,
                                children: <Card style={{ borderTop: 0 }}>
                                    <BasicDetails {...{categoryType,lifescience,labware,gases,labinstrument, data, handleChange, save, brands, setData, setFile, chemicals, thumbFile, setThumbFile, file,setDescriptionFile,descriptionFile }} />
                                </Card>,
                            },
                            {
                                label: "Chemical Attributes",
                                key: 1,
                                disabled: data?.isAttribute ? !data?.isAttribute : true ,
                                children: <Card style={{ borderTop: 0 }}>
                                    <ChemicalAttributes {...{ data,setChemicalAttr,chemicalAttr, handleChange, save, brands, setData, setFile, chemicals, thumbFile, setThumbFile, file }} />
                                </Card>,
                            },
                        ]}
                    />
                </Spin>
            </Modal>
        </>
    );

});

const BasicDetails = ({categoryType,lifescience,labware,gases,labinstrument, data, handleChange, brands, setData, setFile, chemicals, thumbFile, setThumbFile, file,setDescriptionFile,descriptionFile }) => {

	return (
		<>
			<Form layout="vertical">
				<Row gutter={[12, 2]}>
					<Col span={24}>
						<Form.Item label="Select Varient Type" required>
							<Radio.Group
								buttonStyle="solid"
								value={data.varientType}
								onChange={(e) =>
									handleChange({ varientType: e.target.value })
								}
							>
								<Radio.Button value="normal">Normal</Radio.Button>
								<Radio.Button value="onlyWeight">Only Weight</Radio.Button>
							</Radio.Group>
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item label="Title" required>
							<Input
								placeholder="Methanol"
								value={data.name}
								onChange={(e) => {
									handleChange({ name: e.target.value });
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item label="Slug" required>
							<Input
								placeholder="Slug"
								value={data.slug}
								onChange={(e) => {
									handleChange({
										slug: util.removeSpecialChars(e.target.value),
									});
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item label="Sub Title">
							<Input
								placeholder="Methyl Alcohol"
								value={data.subName}
								onChange={(e) => {
									handleChange({ subName: e.target.value });
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={24}>
						<Row gutter={[12, 2]}>
							<Col span={12}>
								<Card title="Choose Brand" size="small">
									<Form.Item label="Brand">
										<Select
											style={{
												width: "100%",
											}}
											placeholder="Select Brand"
											onChange={(v) => {
												handleChange({ brandId: v });
											}}
											value={data.brandId}
											optionLabelProp="label"
											allowClear={true}
											options={brands || []}
										/>
									</Form.Item>
								</Card>
							</Col>

							{
								categoryType === 'gases' &&
								<Col span={12}>
									<Card title="Choose Gases" size="small">
										<Col span={24}>
											<Form.Item label="Choose Gases" required>
												<Select
													mode="multiple"
													style={{
														width: "100%",
													}}
													placeholder="Select Gases"
													onChange={(v) => {
														setData({ ...data, gasIds: [v] });
													}}
													value={data.gasIds?.[0]}
													optionLabelProp="label"
													allowClear={true}
													options={gases.filter((v) => !v.parentId) || []}
												/>
											</Form.Item>
										</Col>
										{data?.gasIds?.map((v, i) =>
											gases?.find((gs) => v?.includes(gs?.parentId)) ? (
												<Col span={24} key={v}>
													<Form.Item label="Choose Gases" required>
														<Select
															mode="multiple"
															style={{
																width: "100%",
															}}
															placeholder="Select Categories"
															onChange={(v) => {
																if (v) {
																	setData({
																		...data,
																		gasIds: [
																			...data.gasIds.slice(0, i + 1),
																			v,
																		],
																	});
																}
															}}
															value={data.gasIds?.[i + 1]}
															optionLabelProp="label"
															options={
																gases.filter((gs) =>
																	v?.includes(gs.parentId)
																) || []
															}
														/>
													</Form.Item>
												</Col>
											) : null
										)}
									</Card>
								</Col>
							}
							{
								categoryType === 'labware' &&
								<Col span={12}>
									<Card title="Choose Labware" size="small">
										<Col span={24}>
											<Form.Item label="Choose Labware" required>
												<Select
													mode="multiple"
													style={{
														width: "100%",
													}}
													placeholder="Select Labware"
													onChange={(v) => {
														setData({ ...data, labwareIds: [v] });
													}}
													value={data.labwareIds?.[0]}
													optionLabelProp="label"
													allowClear={true}
													options={labware.filter((v) => !v.parentId) || []}
												/>
											</Form.Item>
										</Col>
										{data?.labwareIds?.map((v, i) =>
											labware?.find((gs) => v?.includes(gs?.parentId)) ? (
												<Col span={24} key={v}>
													<Form.Item label="Choose Labware" required>
														<Select
															mode="multiple"
															style={{
																width: "100%",
															}}
															placeholder="Select Categories"
															onChange={(v) => {
																if (v) {
																	setData({
																		...data,
																		labwareIds: [
																			...data.labwareIds.slice(0, i + 1),
																			v,
																		],
																	});
																}
															}}
															value={data.labwareIds?.[i + 1]}
															optionLabelProp="label"
															options={
																labware.filter((gs) =>
																	v?.includes(gs.parentId)
																) || []
															}
														/>
													</Form.Item>
												</Col>
											) : null
										)}
									</Card>
								</Col>
							}

							{
								categoryType === 'labInstument' &&
								<Col span={12}>
									<Card title="Choose Lab Instrument" size="small">
										{data?.labInstrumentIds?.map((v, i) =>
											labinstrument?.find((gs) =>
												v?.includes(gs?.parentId)
											) ? (
												<Col span={24} key={v}>
													<Form.Item label="Choose Lab Instrument" required>
														<Select
															mode="multiple"
															style={{
																width: "100%",
															}}
															placeholder="Select Categories"
															onChange={(v) => {
																if (v) {
																	setData({
																		...data,
																		labInstrumentIds: [
																			...data.labInstrumentIds.slice(
																				0,
																				i + 1
																			),
																			v,
																		],
																	});
																}
															}}
															value={data.labInstrumentIds?.[i + 1]}
															optionLabelProp="label"
															options={
																labinstrument.filter((gs) =>
																	v?.includes(gs.parentId)
																) || []
															}
														/>
													</Form.Item>
												</Col>
											) : null
										)}
									</Card>
								</Col>
							}
							{
								categoryType === 'lifeScience' &&
								<Col span={12}>
									<Card title="Choose Science" size="small">
										<Col span={24}>
											<Form.Item label="Choose Life Science" required>
												<Select
													mode="multiple"
													style={{
														width: "100%",
													}}
													placeholder="Select Life-Science"
													onChange={(v) => {
														setData({ ...data, lifeScienceIds: [v] });
													}}
													value={data.lifeScienceIds?.[0]}
													optionLabelProp="label"
													allowClear={true}
													options={
														lifescience.filter((v) => !v.parentId) || []
													}
												/>
											</Form.Item>
										</Col>
										{data?.lifeScienceIds?.map((v, i) =>
											lifescience?.find((gs) => v?.includes(gs?.parentId)) ? (
												<Col span={24} key={v}>
													<Form.Item label="Choose Life-Science" required>
														<Select
															mode="multiple"
															style={{
																width: "100%",
															}}
															placeholder="Select Categories"
															onChange={(v) => {
																if (v) {
																	setData({
																		...data,
																		lifeScienceIds: [
																			...data.lifeScienceIds.slice(0, i + 1),
																			v,
																		],
																	});
																}
															}}
															value={data.lifeScienceIds?.[i + 1]}
															optionLabelProp="label"
															options={
																lifescience.filter((gs) =>
																	v?.includes(gs.parentId)
																) || []
															}
														/>
													</Form.Item>
												</Col>
											) : null
										)}
									</Card>
								</Col>
							}
						</Row>
					</Col>

					<Col span={24}>
						<Divider />
					</Col>

					<Col span={24}>
						<Form.Item label="Sub Description" required>
							<Input.TextArea
								placeholder="Description"
								value={data.subDescription}
								onChange={(e) => {
									handleChange({ subDescription: e.target.value });
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item label="Description" required>
							<Input.TextArea
								placeholder="Description"
								value={data.description}
								onChange={(e) => {
									handleChange({ description: e.target.value });
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item label="Starting Price" required>
							<Input
								type="number"
								min={"0"}
								placeholder="Starting price"
								value={data.startingPrice}
								onChange={(e) => {
									handleChange({ startingPrice: e.target.value });
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item label="Meta Title" required>
							<Input
								placeholder="Meta title"
								value={data.metaTitle}
								onChange={(e) => {
									handleChange({ metaTitle: e.target.value });
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={8}>
						<Form.Item label="Meta Tag" required>
							<Input
								placeholder="Meta tag"
								value={data.metaTag}
								onChange={(e) => {
									handleChange({ metaTag: e.target.value });
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={24}>
						<Form.Item label="Meta Description" required>
							<Input.TextArea
								placeholder="Meta description"
								value={data.metaDescription}
								onChange={(e) => {
									handleChange({ metaDescription: e.target.value });
								}}
							/>
						</Form.Item>
					</Col>
					<Col span={12}>
						<Form.Item label="Status" required>
							<Select
								value={data.status}
								onChange={(e) => {
									handleChange({ status: e });
								}}
							>
								<Select.Option value={true}>Active</Select.Option>
								<Select.Option value={false}>Inactive</Select.Option>
							</Select>
						</Form.Item>
					</Col>
					<Col span={12}>
						<Form.Item label="Is Product Attribute ??" required>
							<Select
								value={data.isAttribute}
								onChange={(e) => {
									handleChange({ isAttribute: e });
								}}
							>
								<Select.Option value={true}>Yes</Select.Option>
								<Select.Option value={false}>No</Select.Option>
							</Select>
						</Form.Item>
					</Col>
					<Col span={12}>
						<Form.Item label="Is EUD Product ??" required>
							<Select
								value={data.isEUD}
								onChange={(e) => {
									handleChange({ isEUD: e });
								}}
							>
								<Select.Option value={true}>Yes</Select.Option>
								<Select.Option value={false}>No</Select.Option>
							</Select>
						</Form.Item>
					</Col>
					<Col span={4}>
						<Form.Item label="Thumbnail Photo">
							<UploadImage
								{...{ fileList: thumbFile, setFileList: setThumbFile }}
							/>
						</Form.Item>
					</Col>
					{
						categoryType === 'labware' &&
					<Col span={4}>
						<Form.Item label="Description Photo">
							<UploadImage
								{...{ fileList: descriptionFile, setFileList: setDescriptionFile }}
								/>
						</Form.Item>
					</Col>
					}
					<Col span={20}>
						<Form.Item label="Photo">
							<UploadImage
								{...{ fileList: file, setFileList: setFile, count: 20 }}
							/>
						</Form.Item>
					</Col>
				</Row>
			</Form>
		</>
	);
}

