const AddLabel = forwardRef((props, ref) => {
	const [open, setOpen] = useState(false);
	const [data, setData] = useState({ aspect: 1 });

	useImperativeHandle(ref, () => ({
		openForm(dt) {
			setOpen(true);
			setData({ ...dt });
		},
	}));
	console.log("data: ", data);
	return (
		<>
			<Modal
				title={
					!data._id ? `Add User  of ${data.name}` : `Variants of ${data.name}`
				}
				style={{ top: 20 }}
				open={open}
				okText="Save"
				onCancel={() => {
					setOpen(false);
				}}
				destroyOnClose
				maskClosable={false}
				width={1200}
				className="app-modal-body"
			>
				<Varient productId={data._id} varientType={data.varientType} />
			</Modal>
		</>
	);
});
